import Link from "next/link";
import { getFeaturedHospitals } from "../src/lib/data/hospitals";
import { getFeaturedTreatments } from "../src/lib/data/treatments";

export default async function HomePage() {
  const [treatments, hospitals] = await Promise.all([
    getFeaturedTreatments(),
    getFeaturedHospitals(),
  ]);

  return (
    <main className="max-w-6xl mx-auto px-4 py-10 space-y-12">
      <section className="space-y-4">
        <h1 className="text-3xl md:text-4xl font-bold text-gray-900">
          HEALO Medical Concierge
        </h1>
        <p className="text-gray-600">
          Compare trusted treatments and partner hospitals across Korea.
        </p>
        <div className="flex gap-4">
          <Link
            href="/treatments"
            className="px-4 py-2 rounded-full bg-teal-600 text-white text-sm font-semibold"
          >
            Browse Treatments
          </Link>
          <Link
            href="/hospitals"
            className="px-4 py-2 rounded-full bg-white border border-gray-200 text-sm font-semibold"
          >
            Browse Hospitals
          </Link>
        </div>
      </section>

      <section className="space-y-4">
        <div className="flex items-center justify-between">
          <h2 className="text-xl font-semibold">Featured Treatments</h2>
          <Link href="/treatments" className="text-sm text-teal-600">
            View all
          </Link>
        </div>
        <div className="grid gap-4 md:grid-cols-3">
          {treatments.map((treatment) => (
            <Link
              key={treatment.id}
              href={`/treatments/${treatment.id}`}
              className="bg-white rounded-2xl border border-gray-100 p-4 hover:shadow-md transition-shadow"
            >
              <h3 className="text-lg font-semibold text-gray-900">
                {treatment.title}
              </h3>
              <p className="text-sm text-gray-500 mt-1 line-clamp-3">
                {treatment.desc || treatment.fullDescription}
              </p>
              <p className="text-sm text-gray-700 mt-3">
                {treatment.hospitalName}
              </p>
            </Link>
          ))}
          {treatments.length === 0 && (
            <p className="text-sm text-gray-500">No treatments available.</p>
          )}
        </div>
      </section>

      <section className="space-y-4">
        <div className="flex items-center justify-between">
          <h2 className="text-xl font-semibold">Partner Hospitals</h2>
          <Link href="/hospitals" className="text-sm text-teal-600">
            View all
          </Link>
        </div>
        <div className="grid gap-4 md:grid-cols-3">
          {hospitals.map((hospital) => (
            <Link
              key={hospital.id}
              href={`/hospitals/${hospital.id}`}
              className="bg-white rounded-2xl border border-gray-100 p-4 hover:shadow-md transition-shadow"
            >
              <h3 className="text-lg font-semibold text-gray-900">
                {hospital.name}
              </h3>
              <p className="text-sm text-gray-500 mt-1 line-clamp-3">
                {hospital.description}
              </p>
              <p className="text-sm text-gray-700 mt-3">{hospital.location}</p>
            </Link>
          ))}
          {hospitals.length === 0 && (
            <p className="text-sm text-gray-500">No hospitals available.</p>
          )}
        </div>
      </section>
    </main>
  );
}
